const fixationPage = {
  fix: () => {
    if (!document.body.classList.contains('disable-scroll')) {
      const pagePosition = window.scrollY;
      const widthScroll = window.innerWidth - document.body.offsetWidth;

      document.body.classList.add('disable-scroll');
      document.body.dataset.position = pagePosition;
      document.body.style.top = -pagePosition + 'px';
      if (widthScroll > 0) {
        document.body.style.paddingRight = widthScroll + 'px';
        document.querySelectorAll('header').forEach(header => header.style.right = widthScroll + 'px');
      }
    }
  },

  unfix: () => {
    if (document.body.classList.contains('disable-scroll')) {
      const pagePosition = parseInt(document.body.dataset.position, 10);

      document.body.removeAttribute('data-position');
      document.body.style.overflowY = 'auto';
      document.body.style.top = 'auto';
      document.body.style.paddingRight = 0;
      document.body.classList.remove('disable-scroll');
      document.querySelectorAll('header').forEach(header => header.style.right = 0);
      window.scroll({ top: pagePosition, left: 0 });
    }
  }
};


let burg = document.querySelector('.js-burger');

let menu = document.querySelector('.js-header-menu');



function showMobileMenu () {

    if (!burg) return false



    burg.addEventListener('click', function(){

        menu.classList.add('show');

    })

}







document.addEventListener('click', (e) => {



    if(menu.classList.contains('show')){

        const withinBoundaries = e.composedPath().includes(menu);

        const withinBurg = e.composedPath().includes(burg);

    

        if (!withinBoundaries && !withinBurg) {

            menu.classList.remove('show');

        }

    }



    return false

})
/**
 * @typedef {Object} dNode
 * @property {HTMLElement} parent
 * @property {HTMLElement} element
 * @property {HTMLElement} to
 * @property {string} breakpoint
 * @property {string} order
 * @property {number} index
 */

/**
 * @typedef {Object} dMediaQuery
 * @property {string} query
 * @property {number} breakpoint
 */

/**
 * @param {'min' | 'max'} type
 */
function useDynamicAdapt(type = 'max') {
	const className = '_dynamic_adapt_'
	const attrName = 'data-da'

	/** @type {dNode[]} */
	const dNodes = getDNodes()

	/** @type {dMediaQuery[]} */
	const dMediaQueries = getDMediaQueries(dNodes)

	dMediaQueries.forEach((dMediaQuery) => {
		const matchMedia = window.matchMedia(dMediaQuery.query)
		// массив объектов с подходящим брейкпоинтом
		const filteredDNodes = dNodes.filter(({ breakpoint }) => breakpoint === dMediaQuery.breakpoint)
		const mediaHandler = getMediaHandler(matchMedia, filteredDNodes)
		matchMedia.addEventListener('change', mediaHandler)

		mediaHandler()
	})

	function getDNodes() {
		const result = []
		const elements = [...document.querySelectorAll(`[${attrName}]`)]

		elements.forEach((element) => {
			const attr = element.getAttribute(attrName)
			const [toSelector, breakpoint, order] = attr.split(',').map((val) => val.trim())

			const to = document.querySelector(toSelector)

			if (to) {
				result.push({
					parent: element.parentElement,
					element,
					to,
					breakpoint: breakpoint ?? '767',
					order: order !== undefined ? (isNumber(order) ? Number(order) : order) : 'last',
					index: -1,
				})
			}
		})

		return sortDNodes(result)
	}

	/**
	 * @param {dNode} items
	 * @returns {dMediaQuery[]}
	 */
	function getDMediaQueries(items) {
		const uniqItems = [...new Set(items.map(({ breakpoint }) => `(${type}-width: ${breakpoint}px),${breakpoint}`))]

		return uniqItems.map((item) => {
			const [query, breakpoint] = item.split(',')

			return { query, breakpoint }
		})
	}

	/**
	 * @param {MediaQueryList} matchMedia
	 * @param {dNodes} items
	 */
	function getMediaHandler(matchMedia, items) {
		return function mediaHandler() {
			if (matchMedia.matches) {
				items.forEach((item) => {
					moveTo(item)
				})

				items.reverse()
			} else {
				items.forEach((item) => {
					if (item.element.classList.contains(className)) {
						moveBack(item)
					}
				})

				items.reverse()
			}
		}
	}

	/**
	 * @param {dNode} dNode
	 */
	function moveTo(dNode) {
		const { to, element, order } = dNode
		dNode.index = getIndexInParent(dNode.element, dNode.element.parentElement)
		element.classList.add(className)

		if (order === 'last' || order >= to.children.length) {
			to.append(element)

			return
		}

		if (order === 'first') {
			to.prepend(element)

			return
		}

		to.children[order].before(element)
	}

	/**
	 * @param {dNode} dNode
	 */
	function moveBack(dNode) {
		const { parent, element, index } = dNode
		element.classList.remove(className)

		if (index >= 0 && parent.children[index]) {
			parent.children[index].before(element)
		} else {
			parent.append(element)
		}
	}

	/**
	 * @param {HTMLElement} element
	 * @param {HTMLElement} parent
	 */
	function getIndexInParent(element, parent) {
		return [...parent.children].indexOf(element)
	}

	/**
	 * Функция сортировки массива по breakpoint и order
	 * по возрастанию для type = min
	 * по убыванию для type = max
	 *
	 * @param {dNode[]} items
	 */
	function sortDNodes(items) {
		const isMin = type === 'min' ? 1 : 0

		return [...items].sort((a, b) => {
			if (a.breakpoint === b.breakpoint) {
				if (a.order === b.order) {
					return 0
				}

				if (a.order === 'first' || b.order === 'last') {
					return -1 * isMin
				}

				if (a.order === 'last' || b.order === 'first') {
					return 1 * isMin
				}

				return 0
			}

			return (a.breakpoint - b.breakpoint) * isMin
		})
	}

	function isNumber(value) {
		return !isNaN(value)
	}
}
let selectSearchToggle = document.querySelector('.js-select-search')
let selectSearchList = document.querySelector('.js-select-list')
let selectItem = document.querySelectorAll('.js-select-item')
let selectTitle = document.querySelector('.js-select-title')

let searchInput = document.querySelector('.js-search-input')
let searchResult = document.querySelector('.js-search-result')

window.addEventListener('load', function () {

    if (!searchInput) return false

    selectSearchToggle.addEventListener('click', function(){
        selectSearchList.classList.toggle('is-open')
    })

    searchInput.addEventListener('input', function(){

        if(this.value.length > 2){
            searchResult.classList.add('is-open')
        }else{
            searchResult.classList.remove('is-open')
        }
    })

    selectItem.forEach(function(item){
        item.addEventListener('click', function(){
            let text = this.textContent
            selectTitle.textContent = text
            selectSearchList.classList.remove('is-open')
        })
    })
})

window.addEventListener('scroll', function(){
    let body = document.querySelector('body')
    let header = document.querySelector('header');
    let firstScreenHeight = document.querySelector('.front-slider_main').offsetHeight;
    let headerHeight = header.offsetHeight;

    let sumOffset = headerHeight + firstScreenHeight

    if (window.pageYOffset > sumOffset) {
        header.classList.add("fixed");
        body.style.paddingTop = headerHeight + 'px'
      } else {
        header.classList.remove("fixed");
        body.style.paddingTop = ''
      }
})

const initImages = () => {
  typeof lozad == 'function' && lozad('.lozad').observe();
}

const initLazyLoadBlock = function() {
  const selector = '.js-lazy-load-block';

  lozad(selector, {
    load: function(block) {
      const url = block.getAttribute('data-url');

      if (!url) return;

      axios.get(url)
      .then(function(response) {
        const data = response.data;

        block.classList.remove('loading');

        if (data['error']) {
          block.classList.add('error');
          block.innerHTML = '<div class="container">' + data['error'] + '</div>';
          console.warn(data['error']);
        } else {
          block.innerHTML = response.data;
          initDefaultElements();
        }
      })
      .catch(function(error) {
        block.classList.remove('loading');
        block.classList.add('error');
        block.innerHTML = '<div class="container">' + projectOptions.error.default + '</div>';
        console.warn(error);
      });
    }
  }).observe();
}



const initDefaultElements = function(e) {
  initImages(e);
}

const pageReady = function(e) {
  initLazyLoadBlock(e);
  initDefaultElements(e);
  showMobileMenu();
  useDynamicAdapt();
}

document.addEventListener("DOMContentLoaded", pageReady);
